.square {
    background: rgb(255, 251, 189);
    background-image: url('C:\dev\html5-training\client\src\stars.png');
    background-size: cover;
    border: 5px outset rgba(243, 73, 192, 0.6);
    float: left;
    font-size: 100px;
    color: rgb(252, 0, 176);
    font-weight: bold;
    line-height: 34px;
    height: 200px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 200px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }

  button.square:hover {
    animation: rotation 0.5s infinite linear;
    transition-duration: 3s;
   }

   @keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }