 .game {
    display: flex;
    flex-direction: row;
    margin: 70px; 
    margin-left: 100px;
    margin-top: 30px;
  }
  
  .info {
    margin-left: -80px;
    justify-content: right;
    margin-top: -40px; 
  }
  
  @counter-style thumbs {
    system: cyclic;
    symbols: ‚ù§Ô∏è üß° üíõ üíö üíô üíú;
    suffix: " ";
  }
  
  li.history {
    font-size: 23px;
    list-style-type: thumbs;
  }

  li::marker {
    font-size: 30px;
  }

  li button.history {
    display: inline-block;
    font: inherit;
    border: 5px solid rgb(0, 173, 204);
    border-radius: 10px;
    padding: 6px 24px;
    margin-bottom: 12px;
    background-color: rgb(42, 235, 235);
    transition-duration: 0.1s;
    box-shadow: 0 6px rgb(0, 149, 175);
  }
  
  li button.history:hover {
    background-color: rgb(187, 255, 0);
  
  }
  
  li button.history:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }

  img.greeting {
    margin-top: -70px;
    margin-left: 180px;
    animation: move 1s infinite linear;
  }

  img.pony {
    margin-top: 40px;
    animation: move 1s infinite linear;
    overflow: hidden;
    margin-left: -80px;
    margin-right: 20px;
  }

  .status {
    font-size: 40px;
  }


  @keyframes move {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }

  @keyframes pulse {
    0% {
      transform: scale(0.85);
      
    }
  
    70% {
      transform: scale(1);
      
    }
  
    100% {
      transform: scale(0.85);
      
    }
  }

button.open {
    display: inline-block;
    font: inherit;
    font-size: 30px;
    font-weight: bold;
    color: rgb(255, 0, 0);
    border: 5px solid #ff0000;
    border-radius: 30px;
    padding: 6px 24px;
    margin-top: 30px;
    margin-left: 200px;
    overflow: hidden;
    background: linear-gradient(to right, orange , yellow, green, cyan, blue, violet);
    box-shadow: 0 6px 10px #d10000;
    transition-duration: 0.1s;
    animation: pulse 0.5s infinite;
}

button.open:hover {
  background-color: rgb(187, 255, 0);

}

button.close {
  display: inline-block;
    font: inherit;
    border: 5px solid rgb(206, 0, 0);
    border-radius: 10px;
    padding: 6px 24px;
    margin: 20px;
    background-color: rgb(255, 0, 0);
}

img.ponywave {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.modal {
  background: linear-gradient(25deg, rgba(255,0,0,1) 0%, rgba(255,154,0,1) 10%, rgba(208,222,33,1) 20%, rgba(79,220,74,1) 30%, rgba(63,218,216,1) 40%, rgba(47,201,226,1) 50%, rgba(28,127,238,1) 60%, rgba(95,21,242,1) 70%, rgba(186,12,248,1) 80%, rgba(251,7,217,1) 90%, rgba(255,0,0,1) 100%);
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

}

@keyframes fly {

	98.001%, 0% {
                display: block;
		transform: translateX(-200%) translateY(100vh) rotateZ(0deg)
	}

	15% {
		transform: translateX(100vw) translateY(-100%) rotateZ(180deg)
	}

	15.001%, 18% {
		transform: translateX(100vw) translateY(-30%) rotateZ(0deg)
	}

	40% {
		transform: translateX(-200%) translateY(3vh) rotateZ(-180deg)
	}

	40.001%, 43% {
		transform: translateX(-200%) translateY(-100%) rotateZ(-180deg)
	}

	65% {
		transform: translateX(100vw) translateY(50vh) rotateZ(0deg)
	}

	65.001%, 68% {
		transform: translateX(20vw) translateY(-200%) rotateZ(180deg)
	}

	95% {
		transform: translateX(10vw) translateY(100vh) rotateZ(0deg)
	}
}