body {
  font: 30px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-image: linear-gradient(105deg, rgb(235, 98, 194, 0.6) 30%, rgb(24, 192, 226, 0.6) 96%), url('C:\dev\html5-training\client\src\rainbow.png');
  background-size: cover;
  cursor: url("C:/dev/html5-training/client/src/cursorgif.gif") 0 8, auto;
  
}

button {
  
  cursor: url("C:/dev/html5-training/client/src/stargif.gif") 0 8, auto;

}

