h1 {
  margin: 0px;
  outline: 5px black;
  font: 70px "Century Gothic";
  font-weight: bold;
  background: linear-gradient(25deg, rgba(255,0,0,1) 0%, rgba(255,154,0,1) 5%, rgba(208,222,33,1) 10%, rgba(79,220,74,1) 15%, rgba(63,218,216,1) 20%, rgba(47,201,226,1) 25%, rgba(28,127,238,1) 30%, rgba(95,21,242,1) 35%, rgba(186,12,248,1) 40%, rgba(251,7,217,1) 45%, rgba(255,0,0,1) 50%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 2px rgba(0, 0, 0);
}




