.grid {
    display: grid;
    grid-template-rows: repeat(3, min-content);
    grid-template-columns: repeat(3, min-content);
    animation: pulse 3s infinite;
	margin-top: 20px;

  }

  @keyframes pulse {
	0% {
		transform: scale(0.85);
		box-shadow: 0 0 2px 0 rgba(243, 73, 192, 0.6);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 10px 20px rgba(243, 73, 192, 0.6);
	}

	100% {
		transform: scale(0.85);
		box-shadow: 0 0 2px 0 rgba(243, 73, 192, 0.6);
	}
}
